import React, { useEffect, useState } from 'react';
import Header from '../components/Header';
import './AboutPhoto.scss';
import "react-slideshow-image/dist/styles.css";
import { Fade } from 'react-slideshow-image';
import Bottom from '../components/Bottom';
import {useParams} from 'react-router-dom';
import { gql,useQuery } from '@apollo/client';
import FadeImages from '../components/fadeImages';

const GET_PHOTO = gql`
   query getPhoto($id: Float!) {
    getPhoto(id: $id) {
        id
        url
     }
   }
 `;

 const AboutPhoto= ()=>{
   
    const temp = useParams()
    const id:number = parseFloat(temp.id)
  const [images,setImages] = useState([])
const { loading, data } = useQuery(GET_PHOTO, {
  variables: { id:id }
}); 

useEffect(()=>{ // fadeImages
  if(!loading ){
    setImages(data.getPhoto)
    console.log(images)
  }
},[loading,data])
  
  
    return (
    <div className="wrap">
         <Header></Header>
         {loading? "Loading...." : <div className="slide-container">
        <Fade>
          {images.map(image =>{ return(<FadeImages src={image.url}/>) })}
        </Fade>
    </div>      }
          <Bottom></Bottom>

</div> )
}

export default AboutPhoto;