import Bottom from '../components/Bottom';
import Header from'../components/Header';
import React, { useState }  from 'react';
import './Contact.scss'
import { gql, useMutation } from '@apollo/client';

const createClientData = gql` 
mutation createClientData($clientInput:clientInput!)
{createClientData(clientInput:$clientInput)}`;

const Contact = () =>{
    const [inputs,setInputs] = useState({
        name:"",
        email:"",
        phone:"",
        content:""
      })
    const {name,email,phone,content} = inputs;
    const[validate,{loading}] = useMutation(createClientData)
    const mutate =async() =>{
        const {data} =await validate({variables:{clientInput:{name,email,phone,description:content}}})
        return data
    }
    const _onclick =async()=>{
        const data = await mutate()
        console.log(data)
        if (typeof window !== 'undefined') {         window.alert("접수완료")
    }
    }
    const handleChange = (e) => {
        const {value,name} = e.target;
        setInputs({
          ...inputs,
          [name]:value
        });
        console.log(value,name)

      };

    return (<div className="contactWrap">
        <Header ></Header>
        <div className="contactBody">
        <div className="inputbody">
        <img src="https://dronehi.s3.ap-northeast-2.amazonaws.com/background-video/contact1.JPG" alt="이미지 로드 오류"/>
        <img src="https://dronehi.s3.ap-northeast-2.amazonaws.com/background-video/contact2.JPG" alt="이미지 로드 오류"/>
        <div className="input_trigger">
        <input placeholder="업체명 및 성함" onChange={handleChange} value={name}/>
        <input placeholder="EMAIL" onChange={handleChange} value={email}/>
        <input placeholder="전화 번호" onChange={handleChange} value={phone}/>

        </div>
        
        </div>
        <textarea className="description" placeholder="희망 의뢰 내용" onChange={handleChange} value={content}/>
        <button className="Btn" onClick={_onclick}>견적 의뢰</button>
        <a className="kakao"  href="https://open.kakao.com/o/gttcsWOc"><img src="https://dronehi.s3.ap-northeast-2.amazonaws.com/background-video/KakaoTalk_20210101_225157063.png" alt="이미지 로드 오류" ></img></a>
        </div>
        <img className="imga" src ="https://dronehi.s3.ap-northeast-2.amazonaws.com/background-video/%EB%93%9C%EB%A1%A0+%EB%A6%AC%EB%A9%94%EC%9D%B4%ED%81%AC+6+(1+-+1).jpg" alt="이미지 로드 오류"  />
        <img className="imga" src ="https://dronehi.s3.ap-northeast-2.amazonaws.com/background-video/%EA%B1%B0%EC%8A%A4%EB%A5%B8+%EC%8B%9C%EA%B0%84+(1+-+1).jpg" alt="이미지 로드 오류"  />
        <img className="imga" src ="https://dronehi.s3.ap-northeast-2.amazonaws.com/background-video/%EB%93%9C%EB%A1%A0+%EB%A6%AC%EB%A9%94%EC%9D%B4%ED%81%AC+1+(1+-+7).jpg" alt="이미지 로드 오류"  />
        <img className="imga" src ="https://dronehi.s3.ap-northeast-2.amazonaws.com/background-video/%EB%93%9C%EB%A1%A0+%EB%A6%AC%EB%A9%94%EC%9D%B4%ED%81%AC+1+(2+-+7).jpg" alt="이미지 로드 오류"  />

<Bottom></Bottom>
    </div>)
}
export default Contact