import React from 'react';
import Bottom from '../components/Bottom';
import Header from '../components/Header';
import HomeBody from '../components/HomeBody';
import {gql, useQuery} from '@apollo/client';
import "./Home.scss";

const Home=()=>{
     const GET_PHOTOS = gql`{ getphotos{url,id}}` ;
     const {loading, error,data} = useQuery(GET_PHOTOS);
    let images=[]
     console.log(loading,error)
     if(!loading){

        
        images=data.getphotos
     }

    return (<div className="Wrap">
   <Header ></Header>   
   <div className="Body">
   <video autoPlay muted loop><source src ="https://dronehi.s3.ap-northeast-2.amazonaws.com/background-video/drone_1.mp4" type="video/mp4"/></video>

    <div className = "BodyImages">{images.map(image =>{ return<HomeBody image={image.url} name={image.id} ></HomeBody>})}</div>
   </div>
   <Bottom></Bottom>
</div>
 )
}
export default Home;